export default (props) => {
    const matches = useMediaQuery("(min-width: 950px)");
    const matches1 = useMediaQuery("(min-width: 400px)");

    const [value, setValue] = React.useState(options[0]);
    const [inputValue, setInputValue] = React.useState('');


    let [temprature, setTemprature] = useState();
    let [country, setCountry] = useState();
    let [city, setCity] = useState();
    let [humidity, setHumidity] = useState();
    let [conditionText, setConditionText] = useState();
    let [conditionIcon, setConditionIcon] = useState();



    async function checkweather() {
        let resp = await axios.get("http://api.weatherapi.com/v1/current.json?key=1732efb22e17452d8f0133353212812&q=" + value + "&aqi=no")

        console.log(resp.data);

        console.log("API Country:" + resp.data.location.country);
        setCountry(resp.data.location.country)

        console.log("Location name:" + resp.data.location.name);
        setCity(resp.data.location.name);

        setTemprature(resp.data.current.temp_c);

        console.log("Humidity:" + resp.data.current.humidity);
        setHumidity(resp.data.current.humidity)

        setConditionText(resp.data.current.condition.text)
        setConditionIcon(resp.data.current.condition.icon)

    }

    return (
        <Box bgcolor="#F5F6FA" py={5} pb={35}>
            <Container>

                <Box>

                    <Box px={3} py={3}>

                        <Box
                            display='flex'
                            justifyContent='center'>
                            <Autocomplete
                                value={value}
                                onChange={(event, newValue,) => {
                                    setValue(newValue);
                                }}

                                inputValue={inputValue}
                                // onInputChange={checkweather}
                                onInputChange={(event, newInputValue) => {
                                    // checkweather()
                                    setInputValue(newInputValue);

                                }}
                                id="controllable-states-demo"
                                options={options}
                                sx={{ width: 300 }}
                                renderInput={(params) => <TextField {...params} label="Select City" />}
                            />

                        </Box>

                        <Box
                            my={5}
                            display='flex'
                            justifyContent='center'
                        >

                            <Button
                                variant="contained"
                                onClick={checkweather}
                                sx={{
                                    background: "#364a63",
                                    fontWeight: "700",
                                    fontSize: { md: "13px", xs: "11px" },
                                    textTransform: "capitalize",
                                    boxShadow: "none",
                                    "&:hover": { backgroundColor: "#364a63a1" },
                                }}
                            >
                                Check Weather
                                <ArrowForwardIcon style={{ paddingLeft: "5px" }} />
                            </Button>
                        </Box>
                    </Box>
                    <Grid container spacing={4}>
                        <Grid item xs={matches ? 4 : 12}>
                            {/* --------Invesment Account box-------- */}
                            <Box className="card1"
                                px={3}
                                py={3}
                                my={5}
                                border="1px solid"
                                borderColor="divider"
                                borderRadius="4px"
                                bgcolor="white"
                                borderLeft="4px solid #620905"
                                width='80%'
                                height='80%'
                            >
                                <Box color="#526484 " fontSize="18px" fontWeight="500">
                                    Location
                                    <LocationOnIcon
                                        sx={{ verticalAlign: "-3px", marginLeft: "6px" }}
                                    />
                                </Box>

                                <Box
                                    display={matches1 ? "flex" : "block"}
                                    mt={{ md: 2, xs: 1 }}
                                >
                                    <Box ml={matches1 ? 1.5 : 0}>
                                        <Box color="#8094ae" fontSize="14px">
                                            Country:
                                        </Box>
                                        <Box
                                            color="#364a63"
                                            fontSize={{ md: "24px", xs: "22px" }}
                                            fontWeight="550"
                                        >
                                            {country}
                                        </Box>
                                    </Box>


                                    <Box ml={matches1 ? 1.5 : 0}>
                                        <Box color="#8094ae" fontSize="14px">
                                            City:
                                        </Box>
                                        <Box
                                            color="#364a63"
                                            fontSize={{ md: "24px", xs: "22px" }}
                                            fontWeight="550"
                                        >
                                            {city}
                                        </Box>
                                    </Box>
                                </Box>
                                {/* ----End Location box----- */}
                            </Box>
                            {/* --------END box-------- */}
                        </Grid>



                        <Grid item xs={matches ? 4 : 12}>
                            {/* --------Temprature  box-------- */}
                            <Box className="card1"
                                px={3}
                                py={3}
                                my={5}
                                border="1px solid"
                                borderColor="divider"
                                borderRadius="4px"
                                bgcolor="white"
                                borderLeft="4px solid #4600DA"
                                width='80%'
                                height='80%'
                            >
                                <Box color="#526484 " fontSize="18px" fontWeight="500">
                                    Temprature
                                    <DeviceThermostatIcon
                                        sx={{ verticalAlign: "-3px", marginLeft: "6px" }}
                                    />
                                </Box>


                                <Box
                                    display={matches1 ? "flex" : "block"}
                                    mt={{ md: 2, xs: 1 }}
                                >
                                    <Box>
                                        <Box
                                            color="#364a63"
                                            fontSize={{ md: "24px", xs: "22px" }}
                                            fontWeight="550"
                                            display="flex"
                                            alignItems="center"
                                        >
                                            <CountUp start={0} end={temprature} duration={1} />
                                            { }
                                            <Box
                                                sx={{
                                                    color: "#526484",
                                                    fontSize: { md: "20px", xs: "18px" },
                                                    fontWeight: "300"
                                                }}
                                                ml={1}
                                            >
                                                °C
                                            </Box>
                                        </Box>

                                    </Box>
                                    <Box ml={matches1 ? 4 : 0} mt={matches1 ? 1 : 1}>
                                        {/* <AddIcon sx={{ color: "#8094ae" }} /> */}
                                    </Box>
                                    <Box ml={matches1 ? 1.5 : 0} >
                                        <Box color="#8094ae" fontSize="14px">
                                            Humidity:
                                        </Box>
                                        <Box
                                            color="#364a63"
                                            fontSize={{ md: "24px", xs: "22px" }}
                                            fontWeight="550"
                                        ><CountUp start={0} end={humidity} duration={1} />

                                        </Box>
                                    </Box>
                                </Box>
                            </Box>
                        </Grid>


                        <Grid item xs={matches ? 4 : 12}>
                            {/* --------Temprature  box-------- */}
                            <Box className="card1"
                                px={3}
                                py={3}
                                my={5}
                                border="1px solid"
                                borderColor="divider"
                                borderRadius="4px"
                                bgcolor="white"
                                borderLeft="4px solid #FFE168"
                                width='80%'
                                height='80%'
                            >
                                <Box color="#526484 " fontSize="18px" fontWeight="500">
                                    Condition

                                    <AcUnitIcon
                                        sx={{ verticalAlign: "-3px", marginLeft: "6px" }}
                                    />
                                </Box>


                                <Box
                                    display={matches1 ? "flex" : "block"}
                                    mt={{ md: 2, xs: 1 }}
                                >
                                    <Box>
                                        <Box
                                            color="#364a63"
                                            fontSize={{ md: "24px", xs: "22px" }}
                                            fontWeight="550"
                                            display="flex"
                                            alignItems="center"
                                        >
                                            {conditionText}
                                        </Box>

                                    </Box>

                                    <Box ml={matches1 ? 4 : 0} mt={matches1 ? 1 : 1}>
                                        {/* <AddIcon sx={{ color: "#8094ae" }} /> */}
                                    </Box>

                                    <Box ml={matches1 ? 1.5 : 0} >
                                        <Box color="#8094ae" fontSize="14px">
                                            <img src={conditionIcon} alt="Icon" />
                                        </Box>
                                    </Box>
                                </Box>
                            </Box>
                        </Grid>



                    </Grid>
                </Box>
            </Container>


        </Box>


    );
};
